.App {
  margin: 0;
  padding: 0;
  border: 0;
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Nav {
  justify-content: flex-start;
  align-items: center;
  height: 40px;
  width: 100vw;
  display: flex;
  background-color: #282c34;
  color: azure;
}

.Nav a {
  color: azure;
  text-decoration: none;
}

.hamburger {
  padding-left: 5px;
}

.hamburger :hover {
  cursor: pointer;
}

.active {
  display: flex;
  width: 100vw;
  visibility: hidden;
  justify-content: flex-start;
  align-items: center;
}

.link-item {
  padding: 5px;
}
